<template>
  <div class="songListMore">
    <div class="top">
      <img
        src="../../assets/images/icon/returnKey.png"
        alt=""
        @click="$router.go(-1)"
      />
      <span>歌单广场</span>
    </div>
    <div class="nav">
      <!-- <div class="shade_nav"></div> -->
      <ul>
        <li :class="urlIndex == 0 ? 'active' : ''" @click="urlIndex = 0">
          推荐
        </li>
        <li :class="urlIndex == 1 ? 'active' : ''" @click="urlIndex = 1">
          最热
        </li>
        <li :class="urlIndex == 2 ? 'active' : ''" @click="urlIndex = 2">
          华语
        </li>
        <li :class="urlIndex == 4 ? 'active' : ''" @click="urlIndex = 4">
          流行
        </li>
        <li :class="urlIndex == 3 ? 'active' : ''" @click="urlIndex = 3">
          欧美
        </li>
      </ul>
    </div>
    <div class="shade"></div>

    <div class="content">
      <Loading v-if="isLoading"></Loading>

      <ul>
        <li v-for="(item, index) in imgItem" :key="index">
          <router-link :to="`/particulars/${item.id}`">
            <span
              ><i>{{ item.playCount | formatNum }}</i></span
            >
            <img
              v-if="urlIndex == 0"
              :src="item.picUrl + '?param=140y140'"
              alt=""
            />
            <img
              v-if="urlIndex != 0"
              :src="item.coverImgUrl + '?param=140y140'"
              alt=""
            />
            <p>{{ item.name }}</p>
          </router-link>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import Loading from "@/components/Loading";
export default {
  name: "SongListMore",
  components: {
    Loading,
  },
  data() {
    return {
      isLoading: true,
      urlArr: [
        "/personalized",
        "/top/playlist?limit=10&order=hot",
        "/top/playlist?limit=10&cat=华语",
        "/top/playlist?limit=10&cat=流行",
        "/top/playlist?limit=10&cat=欧美",
      ],
      urlIndex: 0,
      imgItem: [],
    };
  },
  filters: {
    formatNum(value) {
      return (value / 10000).toFixed(1) + "万";
    },
  },

  watch: {
    urlIndex: function(val) {
      this.isLoading = true;
      this.imgItem = [];
      this.$axios
        .get(this.urlArr[val])
        .then((data) => {
          if (this.urlIndex == 0) {
            return data.data.result;
          } else {
            return data.data.playlists;
          }
        })
        .then((item) => {
          this.imgItem = item;
          this.isLoading = false;
        });
    },
  },
  created() {
    this.$axios
      .get("/personalized")
      .then((data) => {
        return data.data.result;
      })
      .then((item) => {
        this.imgItem = item;
        this.isLoading = false;
      });
  },
};
</script>
<style lang="less" scoped>
.songListMore {
  .shade {
    height: 86px;
    // background-color: burlywood;
  }
  .top {
    width: 94%;
    padding: 10px 3%;
    position: fixed;

    background-color: white;
    z-index: 2;
    img {
      float: left;
      margin-right: 10px;
    }
    span {
      display: block;
      text-align: left;
      padding: 4px 10px;
      font-weight: 400;
      font-size: 1.2rem;
    }
  }
  .nav {
    width: 100%;
    position: fixed;
    top: 50px;
    background-color: white;
    z-index: 1;
    padding: 10px 0;
    // .shade_nav{
    //     height: 70px;
    // }
    ul {
      padding: 0 3%;
      display: flex;
      li {
        flex: 1;
        float: left;
        margin-left: 8px;
        border-radius: 20px;
        background-color: #e9e9e9;
        &:first-child {
          margin-left: 0;
        }
      }

      li.active {
        color: red;
        background-color: #fbdfda;
      }
    }
  }
  .content {
    padding-bottom: 40px;
    .loading {
      margin-top: 200px;
    }
    ul {
      padding: 3%;
      display: flex;
      justify-content: space-between;
      //   justify-content:center;
      flex-wrap: wrap;
      li {
        float: left;
        width: 120px;
        height: 170px;
        overflow: hidden;
        position: relative;
        margin-bottom: 12px;
        // margin-right: 6px;
        img {
          display: block;
          border-radius: 14px;
          width: 120px;
        }
        p {
          width: 118px;
          text-align: left;
          font-size: 0.7rem;
          padding: 4px;
          color: #454545;
        }
        span {
          position: absolute;
          top: 0;
          right: 0;
          font-size: 0.8rem;
          margin: 5px;
          padding: 2px 4px;
          padding-left: 20px;
          border-radius: 20px;
          background-color: rgba(126, 123, 123, 0.6);
          i {
            font-style: normal;
            color: rgb(236, 236, 236);
          }
          &::before {
            content: "";
            width: 20px;
            height: 21px;
            position: absolute;
            left: 2px;
            top: 0;
            background-image: url(../../assets/images/icon/songlisticon.png);
            background-repeat: no-repeat;
            background-size: 80%;
            background-position: center;
          }
        }
      }
    }
  }
}
</style>
